*{
    margin: 0;
    padding: 0;
}

@font-face{
    font-family: 'montserrat';
    src: url('https://fonts.gstatic.com/s/montserrat/v15/JTURjIg1_i6t8kCHKm45_epG3gnD_g.woff2');
}

@font-face{
    font-family: 'muli';
    src: url('https://fonts.gstatic.com/s/muli/v22/7Auwp_0qiz-afTLGLQ.woff2');
}

.about-me-holder{
    height: 100vh;
    display: flex;
    align-items: center;
    padding: 0 40px;
    justify-content: space-around;
}

.about-info{
    width: 100%;
    position: relative;
    z-index: 1;
}

.about-info h1{
    margin: 0;
    font-family: 'montserrat';
    font-size: 50px;
    color: #C7493A;
    letter-spacing: 3px;
}

.about-info p{
    font-size: 27px;
    line-height: 20px;
    margin: 20px 0px 30px 0px;
    letter-spacing: 1px;
    color: white;
    font-family: 'muli';
}

.about-info button{
    padding: 15px 25px;
    width: 200px;
    border-radius: 0;
    border: transparent;
    font-size: 15px;
    outline: transparent;
    font-family: 'muli';
    cursor: pointer;
    letter-spacing: 1.3px;
}


.about-name{
    position: relative;
}

.about-name::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 30px;
    bottom: 0;
}

.introduction{
    font-size: 100px !important;
}

.tsparticles-canvas-el{
    position: absolute;
    top: 0;
    background-color: #000000;
    z-index: 0;
    left: 0;
}

.particles{
    height: 100%;
}

@media(max-width: 808px){
    .about-info h1{

        font-size: 40px;
    
    }
    
    .about-info p{
        font-size: 20px;
      
    }

    
}

@media(max-width: 678px){
    .about-me-holder{
      
        padding: 0 20px;
     
    }
    
    .about-info h1{

        font-size: 30px;
    
    }
    
    .about-info p{
        font-size: 20px;
      
    }


    .introduction{
        font-size: 60px !important;
    }
    
}

@media(max-width: 490px){

    
    .about-info h1{

        font-size: 24px;
    
    }
    
    .about-info p{
        font-size: 15px;
        margin: 15px 0px;
      
    }

    
}