.nav-holder{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    border-top: .5px solid #c7493a;
}

.nav-holder ul{
    width: 100%;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
    height: 60px;
    text-align: center;
    background: #000000b0;
}

.nav-holder a{
    font-size: 15px;
    font-family: "montserrat";
    letter-spacing: 2px;
    color: #c7493a;
    width: 100%;
    padding: 10px;
    display: flex;
    height: 100%;
    position: relative;
    z-index: 1!important;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
    align-items: center;
}

.nav-holder a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 0%;
    top: 0;
    left: 0;
    background: #c7493a;
    transition-duration: .3s;
    z-index: -1!important;
}



.nav-holder a:hover::before{
height: 100%;
}
.nav-holder a:hover {
    color: #000;
}




@media(max-width: 808px){
    .nav-holder a{
        font-size: 13px;
      
    }
    
}

@media(max-width: 700px){
    .nav-holder a{
        font-size: 10px;
      
    }
    
}

