.contact-holder{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact{
    background: #c7493a;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 500px;
    height: 500px;
    padding: 15px;
    position: relative;


  
}

.contact::before{
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    width: 0;
    height: 0;
    background: transparent;
    border: 3px solid transparent;
    animation: border 2s linear forwards;
    animation-iteration-count: infinite;

}

@keyframes border{
    0%{
        width: 0;
        height: 0;
        border-top-color: transparent;
        border-right-color: transparent;
        border-bottom-color: transparent;
        border-left-color: transparent;
    }
    50%{
        width: 100%;
        height: 0;
        border-top-color: white;
        border-right-color: white;
        border-bottom-color: transparent;
        border-left-color: transparent;
    } 
    100%{
        width: 100%;
        height: 100%;
        border-top-color: white;
        border-right-color: white;
        border-bottom-color: transparent;
        border-left-color: transparent;
    } 
}

.contact::after{
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    width: 0;
    height: 0;
    background: transparent;
    border: 2px solid transparent;
    animation: border-after 2s linear forwards;

    animation-iteration-count: infinite;

}

@keyframes border-after{
    0%{
        width: 0;
        height: 0;
        border-top-color: transparent;
        border-right-color: transparent;
        border-bottom-color: transparent;
        border-left-color: white;
    }
    50%{
        width: 0;
        height: 100%;
        border-top-color: transparent;
        border-right-color: transparent;
        border-bottom-color: white;
        border-left-color: white;
    } 
    100%{
        width: 100%;
        height: 100%;
        border-top-color: transparent;
        border-right-color: transparent;
        border-bottom-color: white;
        border-left-color: white;
    } 
}

.contact-h1{
    letter-spacing: 1px;
    margin-bottom: 20px;
    font-size: 40px;

}

.contact  a{
    font-size: 18px;
    display: flex;
    align-items: center;
}


.contact ul li{
    border-bottom: 2px solid black;
    padding: 20px 0px;
    list-style: none;
    overflow-wrap: anywhere;
}

.contact ul li h1{
    margin-bottom: 10px;
}

.contact ul li a{
    text-decoration: none;
    color: black;
    
}

@media(max-width: 586px){
    .contact{
      
        width: 320px;
        height: 320px;
        padding: 40px;
       
    }

    .contact-h1{
        font-size: 20px;
        
    
    }
    
    .contact  a{
        font-size: 15px;
        display: flex;
        align-items: center;
    }

    .svg{
       
        width: 25px !important;
        margin-right: 10px;
      
    }

    .contact ul li h1{
       font-size: 20px;
       display: flex;
       align-items: center;
       
    }

    
    
    
}

@media(max-width: 430px){
    .contact{
      
      width: 250px;
      height: 250px;
        padding: 100px 20px;
       
    }
}