@font-face{
    font-family: 'montserrat';
    src: url('https://fonts.gstatic.com/s/montserrat/v15/JTURjIg1_i6t8kCHKm45_epG3gnD_g.woff2');
}

@font-face{
    font-family: 'muli';
    src: url('https://fonts.gstatic.com/s/muli/v22/7Auwp_0qiz-afTLGLQ.woff2');
}

@font-face{
    font-family: 'rale';
    src: url('https://fonts.gstatic.com/s/raleway/v18/1Ptug8zYS_SKggPNyC0ITw.woff2');
}

body{
    overflow-x: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;  /* Firefox */
    background-color: black;

}
body::-webkit-scrollbar{
    display: none;
}

.profile-landing{
    display: grid;
}

.profile-photo{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
}

.profile-photo img{
    width: 250px;
    height: 250px;
    object-fit: cover;
    border-radius: 50%;
    padding: 10px;


}

.profile-info-button{
    padding: 15px 25px;
    width: 200px;
    margin-top: 30px;
    border-radius: 0;
    border: transparent;
    font-size: 15px;
    outline: transparent;
    font-family: 'muli';
    letter-spacing: 1.3px;
    cursor: pointer;
}

.rotating{
    width: 250px;
    height: 250px;
    position: relative;
    margin-bottom: 30px;
    
}

.rotating::before{
    content: '';
position: absolute;
width: 100%;
height: 100%;
border: 10px transparent solid ;
border-left-color: #C7493A;
    border-right-color: #C7493A;
    border-radius: 50%;
    animation-name: rotate;
    animation-duration: 5000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear; 
}

@keyframes rotate {
    from {
        transform:rotate(0deg);
  
    }
    to {
        transform:rotate(360deg);
 
    }
}

.profile-header h1{
    font-size: 40px;
    letter-spacing: 2px;
    font-family: 'montserrat';
    margin-top: 100px;
    color:   #C7493A;
}

.profile-number{
    color: WHITE;
}

.profile-photo p{
    width: 70%;
    text-align: center;
    font-family: 'rale';
}

.profile-info{
    padding: 0 50px;
    font-size: 20px;
    letter-spacing: 1px;
    color: white;
    margin-top: 10px;
    font-family: 'muli';
    text-align: justify;
}

.profile-info span{
    font-size: 25px;
    font-family: 'montserrat';
    letter-spacing: 1px;
    color: #c7493a;
}

.profile-skill-img{
    margin-top: 50px;
    padding: 0 40px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;

}

.profile-skill-img img{
    width: 150px;
    height: 150px;
    object-fit: cover;
    margin-top: 50px;
}

.project{
    display: grid;
    grid-template-columns: 30% 60%;
    margin-top: 50px;
    place-content: center;
    padding-bottom: 50px;
}

.project-images img{
    width: 100%;
    height: 100%;
}


.slick-slider.slick-initialized{
    display: grid ;
}

.slick-slide.slick-active.slick-current{
    border: transparent;
}

.slide-image{
    outline: transparent;
}

.project-images{
    border-top: 10px solid #C7493A;
}

.profile-projects-info{
    text-align: left;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 50px;
}

.profile-projects-info h1{
    margin-bottom: 10px;
    font-family: 'montserrat';
    font-size: 30px;
    color:  #C7493A;
}

.profile-projects-info p{
    font-family: 'muli';
    font-size: 18px;
    color: white;
    letter-spacing: 1px;
}

.project-images-2{
    height: 455px;
    overflow-y: scroll;
     border-top: 10px solid #C7493A;
}

.project-images-2 img{
    height: 1000px;
    width: 100%;
    object-fit: fill;

}

.project-images-2::-webkit-scrollbar{
    display: none;
    -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;
}

.tsparticles-canvas-el{
    z-index: -1;
}

button.slick-arrow.slick-next{
    display: none !important;
}

.slick-prev:before{
    display: none !important;

}

@media(max-width: 768px){
    .profile-photo p {
        width: 80%;
    }

    .profile-skill-img {
        grid-template-columns: 1fr 1fr;
    }

    .project {     
        grid-template-columns: 1fr;
    }
    br{
        display: none;
    }


    .project-images-2{
        margin-bottom: 20px;
    }

    .project-images{
        margin-bottom: 20px;
    }


    .project{
        overflow-wrap: anywhere !important;
    }
}

@media(max-width: 480px){
    .profile-projects-info h1{
        text-align: center;
        font-size: 25px;
        letter-spacing: 1px;
    }
    .slide-image{
        display: flex !important;
        justify-content: center;
    }
    .project-images img{
        width: 100%;
    }
    .project-images-2 {
        display: flex !important;
        justify-content: center;
        height: 317px;
    }

    .project-images-2 img{
        width: 100%;
    }
    .profile-projects-info p{
        text-align: center;
    }

    br{
        display: block !important;
    }
    .profile-photo{
        margin-top: 100px;
    }
    .profile-skill-img img {
        width: 100px;
        height: 100px;
        
    }
    .profile-info {
        padding: 0 30px;
       
    }

    .profile-projects-info {
        padding: 0 20px;
    }

    .profile-photo img {
        width: 200px;
        height: 200px;
       
    }

    .rotating{
        width: 200px;
        height: 200px;
      
        
    }

    .profile-info span{
        font-size: 20px;
       
    }
}

@media(max-width: 320px){
    

.profile-header h1 {
    font-size: 30px;
  
}

.profile-skill-img{
    padding: 0 !important;
}

.profile-info {
    padding: 0 15px;
}
.profile-photo p {
    width: 90%;
}

.profile-photo {
    margin-top: 50px;
}

.profile-info{
    font-size: 19px;
}
}